const searchInput = $ => {
	const menuClass = 'header__menu';
	const navClass = 'header__navigation';
	const searchClass = 'header__search';
	const inputClass = 'search-input__field';
	const buttonClass = 'search-input__button';
	const hiddenMod = '_hidden';

	const menu = $('.' + menuClass)
	const navigation = $('.' + navClass);
	const search = $('.' + searchClass);
	const input = $('.' + inputClass);
	const button = $('.' + buttonClass);
	const jqWindow = $(window);

	button.on('click', function (){
		navigation.toggleClass(navClass + hiddenMod);
		search.toggleClass(searchClass + hiddenMod);
		input.toggleClass(inputClass + hiddenMod);
		button.toggleClass(buttonClass + hiddenMod);
	});


	const layout = {
		type: 'desktop',
		change(type) {
			if (this.type === type) {
				return;
			}

			const navHeight = Number(navigation.css('height').slice(0, -2));
			if (type === 'mobile') {
				this.type = type;
				
				if (navHeight > 44) {
					const diff = navHeight - 44;
					search.css('top', `${diff + 70}px`);
					menu.css('height', `${diff + 120}px`);
				}
				input.removeClass(inputClass + hiddenMod);
				button.css('display', 'none');
			}else if (type === 'desktop') {
				this.type = type;
				if (navHeight > 44) {
					search.css('top', '70px');
					menu.css('height', '120px');
				}
				input.addClass(inputClass + hiddenMod);
				button.css('display', 'inline-block');
			}
		}
	};

	const fixLayout = () => {
		if (jqWindow.width() <= 600) {
			layout.change('mobile');
		}else {
			layout.change('desktop');
		}
	};

	// init
	fixLayout();


	jqWindow.on('resize', function (){
		fixLayout();
	});
};

module.exports = searchInput;



